.app-wrapper {
    width: 900px;
    margin: 0 auto;
    display: grid;
    grid-template-areas: "h h" "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

h2 {
    font-size: 20px;
    margin-block-start: 0.1em;
    margin-block-end: 0.1em;
}

.app-wrapper-content {
    background: cornflowerblue;
    grid-area: c;
}


